import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import logo from '../../assets/logo.svg';
import './home.less';
import Hello from '../hello/hello';

//列表循环
const users = [
  { username: 'Jerry', age: 21, gender: 'male' },
  { username: 'Tomy', age: 22, gender: 'male' },
  { username: 'Lily', age: 19, gender: 'female' },
  { username: 'Lucy', age: 20, gender: 'female' }
]

class User extends Component {
  // constructor(props) {
  //   super(props);
  // }

  handleClick = (user,index) => {
    console.log(user,index)
  }

  render () {
    let user = this.props.user;
    let index = this.props.index;
    return (
      <div className="m-t-20" onClick={this.handleClick.bind(this,user,index)}>
        <div>姓名：{user.username}</div>
        <div>年龄：{user.age}</div>
        <div>性别：{user.gender}</div>
        <div>序号：{index}</div>
      </div>
    )
  }
}

class Index extends Component {
  render () {
    return (
      <div>
        {users.map((item,i) => <User key={i} index={i} user={item} />)}
      </div>
    )
  }
}

//tab切换
class TabsControl extends React.Component{

    constructor(){
        super();
        this.state={ 
            currentIndex : 0
        };
    }

    check_tittle_index(index){
        return index === this.state.currentIndex ? "active" : "";
    }

    check_item_index(index){
        return index === this.state.currentIndex ? "show" : "Tab_item";
    }

    render(){
        return(
            <div>
                {/*动态生成Tab导航*/}
                <div className="Tab_tittle_wrap">
                    { React.Children.map( this.props.children , (element,index) => {
                        return(
                            /*箭头函数没有自己的this，这里的this继承自外围作用域，即组件本身*/
                            <div onClick={ () => { this.setState({currentIndex : index}) } } className={ this.check_tittle_index(index) }>{ element.props.name }</div>
                            );
                    }) }
                </div>
                {/*Tab内容区域*/}
                <div className="Tab_item_wrap">
                    {React.Children.map(this.props.children,(element,index)=>{
                        return(
                            <div className={ this.check_item_index(index) }>{ element }</div>
                            );
                    })}
                </div>
            </div>
            );
    }
}



//渲染页面
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 0,
      value:'123',
      selected:'coconut',
      list:[
        {name:'导航一',id:1},
        {name:'导航二',id:2},
        {name:'导航三',id:3},
      ],
      currentIndex:1,
    }
  }
  
  handleAdd = () => {
    this.setState({ count: this.state.count + 1 });
  }

  handleZero = () => {
    this.setState({ count: 0 });
  }
  
  handleChange = e => {
    this.setState({ value: e.target.value });
  }

  handleSelect = e => {
    this.setState({ selected: e.target.value });
  } 

  handleClick = i => {
    this.setState({
      currentIndex:i+1
    });
  }

  getArr = () => {
    var a = [1,2,3,4];
    return a;
    
  }
  
  render() {
    let html = <h1>标题<span>123</span></h1>;
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" title="logo"/>
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <h1><Link to="/lesson1/lee" target="_blank">新开页面，跳转lesson1</Link></h1>
        <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p>
        <div className="App-box">
          {html}
          {<Hello count={this.state.count} name="组件通信"/>}
          {this.state.count}
          <button onClick={this.handleAdd}>加</button>
          <button onClick={this.handleZero}>清零</button>
          {<div className="m-t-20">
            <form>
              <input type="text"  value={this.state.value} onChange={this.handleChange}/>
            </form>
            <p>{this.state.value}</p>
          </div>}
          <div className="m-t-20" style={{color:"red"}}>
            <select  value={this.state.selected} onChange={this.handleSelect}>
              <option value="grapefruit">Grapefruit</option>
              <option value="lime">Lime</option>
              <option value="coconut">Coconut</option>
              <option value="mango">Mango</option>
            </select>
            <p>{this.state.selected}</p>
          </div>
        </div> 

        <div className="m-t-20">
          <h1>列表循环</h1>  
          <Index />
        </div>

         <div className="m-t-20">
          <h1>tab切换</h1>  
          <ul className="tab-list">
              {
                this.state.list.map((item,i) => {
                  var tabStyle = item.id === this.state.currentIndex ? 'active' : '';
                  return <li key={i} onClick={this.handleClick.bind(this,i)} className={tabStyle}>{item.name}</li>
                })
              }
          </ul>
           <TabsControl>
              <div name="社会新闻">
                  <span>社会新闻的内容</span>
              </div>
              <div name="体育世界">
                  体育世界的内容
              </div>
              <div name="娱乐圈">
                  娱乐圈的内容
              </div>
          </TabsControl>
        </div>
      </div>
    );
  }
}

export default App;



